<template>
    <div>
        <el-main>
            <div class="select">
                <el-input v-model="rid" placeholder="编号" clearable></el-input>
                <el-input v-model="name" placeholder="角色名" clearable></el-input>
            </div>
            <div class="btn">
                <el-button type="primary" icon="el-icon-plus" @click="add()">添加</el-button>
            </div>
            <el-table
                border
                tooltip-effect="dark"
                style="width: 100%"
                :header-cell-style="{background:'#f5f7fa'}"
                :default-sort="{prop: 'rid'}"
                :data="tableData"
            >
                <el-table-column prop="rid" label="编号" sortable></el-table-column>
                <el-table-column prop="name" label="角色名"></el-table-column>
                <el-table-column prop="pagePower" label="页面权限"></el-table-column>
                <el-table-column prop="depPower" label="部门权限"></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                            size="mini"
                            type="primary"
                            @click="handleEdit(scope.$index, scope.row)"
                        >编辑</el-button>
                        <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)"
                        >删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                style="float: right;margin-top:8px;"
                background
                :current-page.sync="currentPage"
                @current-change="handleCurrentChange"
                layout="prev, pager, next"
                :page-size="7"
                :total="totalPage"
            ></el-pagination>
        </el-main>
        <ppmm :ppmmitem="ppmmitem" :ppmmVisible="ppmmVisible" @close-dialogStatus="Close_dialog"></ppmm>
    </div>
</template>

<script>
import router from "../router";
import ppmm from "@/components/dialog/ppmm";
import axios from "axios";
export default {
    data() {
        return {
            ppmmitem: {},
            ppmmVisible: false,
            rid: "",
            name: "",
            pagePower: "",
            depPower: "",
            currentPage: 1,
            totalPage: 1,
            tableData: [],
            departmentList: []
        };
    },
    components: {
        ppmm
    },
    created: function() {
        this.getList();
        this.getDepartment();
    },
    watch: {
        rid(newValue, oldValue) {
            this.getList();
        },
        name(newValue, oldValue) {
            this.getList();
        }
    },
    methods: {
        getList() {
            let here = this;
            axios({
                url: "/rolelist",
                params: {
                    rid: here.rid,
                    name: here.name,
                    currentPage: here.currentPage
                }
            })
                .then(function(response) {
                    let data = response.data;
                    if (data.totalPage === -1) {
                        here.$message.error("您没有权限!");
                    } else {
                        here.tableData = data.data;
                        here.totalPage = data.totalPage;
                    }
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        getDepartment() {
            let here = this;
            axios({
                url: "/departments"
            })
                .then(function(response) {
                    here.departmentList = response.data;
                })
                .catch(function(error) {
                    here.$message.error("无法连接服务器!");
                });
        },
        add() {
            this.ppmmVisible = true;
        },
        Close_dialog(val) {
            this.ppmmVisible = false;
            this.getList();
        },
        handleEdit(index, row) {
            let here = this;
            axios({
                url: "/edit",
                params: {
                    index: "13"
                }
            })
                .then(function(response) {
                    if (response.data.success === 1) {
                        router.push({
                            name: "powertabs",
                            params: {
                                data: row,
                                departmentList: here.departmentList
                            }
                        });
                    } else {
                        here.$message.error("您不具备该权限!");
                    }
                })
                .catch(function(error) {
                    here.$message.error("无法连接服务器!");
                });
        },
        handleDelete(index, row) {
            let here = this;
            this.$confirm("是否删除该角色?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                axios({
                    url: "/roledelete",
                    params: {
                        rid: row.rid
                    }
                })
                    .then(function(response) {
                        if (response.data === 1) {
                            here.$message({
                                type: "success",
                                message: "删除成功!"
                            });
                            here.getList();
                        } else if (response.data === 0) {
                            here.$message.error("删除失败!");
                        } else {
                            here.$message.error("您没有权限!");
                        }
                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            });
        },
        handleCurrentChange() {
            this.getList();
        }
    }
};
</script>

<style lang="stylus" scoped>
body {
    margin: 0;
}

.btn {
    margin-top: 10px;
    margin-bottom: 10px;
}

.select {
    display: inline;
}

.select, .el-input {
    width: 156px;
}

.search {
    position: relative;
    top: -1px;
}

.el-select .el-select--small {
    margin-left: 0;
}
</style>


